
import Vue from 'vue'
import { pages } from '@/store'
export default Vue.extend({
  props: {
    icon: {
      type: Object,
      required: true,
    },
    actived: {
      type: Boolean,
    },
    index: {
      type: Number,
      required: true,
    },
  },
  computed: {
    pages() {
      return pages
    },
  },
  data() {
    return {
      text: ['List', 'Add', 'Count', 'Cart','Inventory', 'Logs', 'QR'],
    }
  },
})
